export default class DocumentFromWebsite extends BaseHandler {
    constructor() {
        super();
        this.requiredFields = [
            'nameOfWebPage',
            'url',
            'year',
            'month',
            'day',
            'yearAccessed',
            'monthAccessed',
            'dayAccessed'
        ];
        this.otherFields = [
            'nameOfWebsite',
            'producerName',
            'productionCompany'
        ];
    }

    /**
     * Sets web page name.
     *
     * @param {string} data
     * @returns {void}
     */
    setNameOfWebPage = (data = null) => {
        this.allData.nameOfWebPage = data;
    };

    /**
     * Sets website name.
     *
     * @param {string} data
     * @returns {void}
     */
    setNameOfWebsite = (data = null) => {
        this.allData.nameOfWebsite = data;
    };

    /**
     * Sets producer name.
     *
     * @param {string} data
     * @returns {void}
     */
    setProducerName = (data = null) => {
        this.allData.producerName = data;
    };
}
